
//-----------------------
//Header
//-----------------------
.main-header {
    padding: 20px 0;
    margin: 0 auto;
    display: block;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
    background-color:  var(--background-color);
}


.main-header__inner {
    display: flex;
    justify-content: space-between;
    
}

.main-header__logo {
    text-align: center;
}

.main-header__nav {
    display: flex;
    align-self: center;
}

.main-header__name {
    @include link--reversed-color;
}

.main-header__link {
    color: var(--text-color);      
    @include fluid-sizing(320px, 1440px, 20px,24px, 'font-size') ;
    font-family: $font--abril;
    text-transform: uppercase;
    padding-left: 23px;

    &:hover {
        color: var(--link-color);
    }
    
}


//-----------------------
//Header - Nav
//-----------------------

.main-header__nav-list.nav {
    justify-content: flex-end;
}

.nav-list__item {
    padding-left: 23px;
}

.nav-list__print {
    display: none;
    .cv & {
        display: inline-block;
        
        svg {
            transition: all 0.2s;
            fill: var(--text-color); 
        }
        &:hover {
            svg {
                fill: var(--link-color); 
            }
        }
    }
}

// //------ lang
button[class^="language-switch__button--"] {
        color: var(--text-color);
        transition: all 0.5s;
        padding: 0 4px;

    &:hover,
    &:focus,
    &:active {
        color:  var(--background-color);
    }
}

.language-switch__button--nl {
    box-shadow: inset 0 0 0 var(--link-color);
   
    &:hover,
    &:focus,
    &:active {
        box-shadow: inset -10em 0 0  var(--link-color);
    }
}

.language-switch__button--nl--active {
    background:  var(--link-color);
    color: var(--text-color);
}


.language-switch__button--en {
    box-shadow: inset 0 0 0 var(--accent-color);
   
    &:hover,
    &:focus,
    &:active {
        box-shadow: inset 10em 0 0 var(--accent-color);
        color: var(--text-color);

    }
}

.language-switch__button--en--active {
    background: var(--accent-color);
    color: var(--text-color);
}



/////
.switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 15px;
    margin-top: 6px;
    margin-bottom: 0;
    input {
        opacity: 0;
        width: 0;
        height: 0;
      }
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;

  &:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    top: 50%;
    transform: translateY(-50%);
    background-color: var(--link-color);
    -webkit-transition: .4s;
    transition: .4s;
  }
}


input:checked+.slider {
  background-color: var(--accent-color);
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(11px)translatey(-50%);
  -ms-transform: translateX(11px)translatey(-50%);
  transform: translateX(11px)translatey(-50%);
}

/* Rounded sliders */
.slider{
    &.round {
        border-radius: 34px;
        &:before {
            border-radius: 50%;
          }
      }
}

